/**
 * @description: 
 * @param {*} domName  代表滚动条所对应的dom元素
 * @return {*}
 */
import debounce from '@/utils/debounce';

export default function(domName) {
    return {
        mounted() {

            this.debounceHandle = debounce(this.handleScroll, 0)
            window.addEventListener("scroll", this.debounceHandle)
                // console.log(this.debounceHandle)

            // 注册一个点击重置scrollTop的事件
            this.$bus.$on("resetScrollTop", this.restScrollTopHandle)

        },
        beforeDestroy() {
            // 怎么样让top这个div消失？
            // 我想的是让top值为0就消失了，可是不管用,为什么会完全不触发呢？因为太快了，dom还没来得及触发，dom就被销毁了
            // 那么现在就找其他方法吧，为什么我触发让top值为0就没有用？为什么触发v-show就有用，因为其实我已经让她重置了，但是因为你要让她为0，然后去触发v-show并且v-show是靠滚动的值去改变显示与消失，因为触发太快，而无法去触发v-show这个事件，所以你就直接触发滚动条滚动事件好了
            this.$bus.$emit("scrollTopEvent")
                // this.$bus.$emit("resetScrollTop", this.restScrollTopHandle)
            window.removeEventListener("scroll", this.debounceHandle)
                // this.$bus.$off("resetScrollTop", this.restScrollTopHandle)
        },
        methods: {
            handleScroll() {
                this.$bus.$emit("scrollTopEvent") //触发事件
            },
            // restScrollTopHandle(top) {
            //     console.log("scrollTop", document.documentElement.scrollTop)
            //     document.documentElement.scrollTop = 0;
            // }
        }
    }
}