<script>
import About from "./sectionView/About";
import Culture from "./sectionView/Culture";
import strength from "./sectionView/strength";
import Wish from "./sectionView/Wish";

export default {
  components: {
    About,
    Culture,
    strength,
    Wish,
  },
  data() {
    return {
      activeIndex: 1,
      choiceComponent: ["About", "Wish", "Culture", "strength"],
      titleMsg:[{title: "关于冠亚",msg:"ABOUT US"},{title: "专家渠道",msg:""},],
      zyContentMove: false,
    };
  },
  computed: {
    choiceIndex() {
      return this.choiceComponent[this.activeIndex - 1];
    },
  },
  mounted() {
    this.$bus.$on("scrollTopEvent", this.showIntroduce);
  },
  destroyed() {
    this.$bus.$off("scrollTopEvent", this.showIntroduce);
  },
  methods: {
    async showIntroduce(dom) {
      this.zyContentMove = window.scrollY === 0 ? false : true;
