<script>
import HomeViewIntroduceVue from "@/components/HomeViewIntroduce/HomeViewIntroduce.vue";
import ProductViewVue from "@/components/ProductView/ProductView.vue";
import NewsViewVue from "@/components/NewsView/NewsView.vue";
import countTo from "vue-count-to";
import { clientHeight } from "@/api/clientHeight.js";
export default {
  name: "HomeView",
  components: {
    HomeViewIntroduceVue,
    ProductViewVue,
    NewsViewVue,
    countTo,
  },
  data() {
    return {
      startVal1: 0,
      endVal1: 15,
      endVal2: 50,
      endVal3: 30,
      endVal4: 16,
      endVal5: 3000000000,
      play: false,
    };
  },
  mounted() {
    console.log("clientHeight", clientHeight);
    this.$bus.$on("scrollTopEvent", this.countMove);
  },
  destroyed() {
    this.$bus.$off("scrollTopEvent", this.countMove);
  },
  methods: {
    countMove() {
      let countTop = this.$refs.countMoveRef.getBoundingClientRect().top;
      let glap = countTop - clientHeight;

      if (glap < 0 && !this.paly) {
        this.paly = true;
        this.$refs.count1.start();
        this.$refs.count2.start();
        this.$refs.count3.start();
        this.$refs.count4.start();
        this.$refs.count5.start();
      }
    },
  },
};
</script>
