<script>
import { clientHeight } from "@/api/clientHeight.js";
export default {
  components: {
  },
  data() {
    return {
      isAnimationgTile: false,
      initValue: "visibility: hidden; animation-name: none;",
      isAnimate1: false,
      isAnimate2: false,
      isAnimate3: false,
      isAnimate4: false,
      initValue1: "visibility: hidden; animation-name: none;",
      initValue2: "visibility: hidden; animation-name: none;",
      initValue3: "visibility: hidden; animation-name: none;",
      initValue4: "visibility: hidden; animation-name: none;",
    };
  },
  mounted() {
    this.$bus.$on("scrollTopEvent", this.scrollMove);
    // clientHeight = Object.freeze(document.documentElement.clientHeight);
  },
  destroyed() {
    this.$bus.$off("scrollTopEvent", this.scrollMove);
  },
  methods: {
    titleEnd() {
      this.isAnimate1 = false;
    },
    titleEnd1() {
      this.isAnimate2 = false;
    },
    titleEnd2() {
      this.isAnimate3 = false;
    },
    titleEnd3() {
      this.isAnimate4 = false;
    },
    titleEnd4() {
      this.isAnimationgTile = false;
    },
    async scrollMove(dom) {
      this.isMove = window.scrollY === 0 ? false : true;
      // 标题
      let domTop = this.$refs.articleTitle.getBoundingClientRect().top;
      // 第一个盒子
      let boxTop1 = this.$refs.box1.getBoundingClientRect().top;
      // 第二个盒子
      let boxTop2 = this.$refs.box2.getBoundingClientRect().top;
      // 第三个盒子
      let boxTop3 = this.$refs.box3.getBoundingClientRect().top;
      // 第四个盒子
      let boxTop4 = this.$refs.box4.getBoundingClientRect().top;
      let glap = domTop - clientHeight;
      let glap1 = boxTop1 - clientHeight;
      let glap2 = boxTop2 - clientHeight;
      let glap3 = boxTop3 - clientHeight;
      let glap4 = boxTop4 - clientHeight;

      if (glap <= 0 && !this.isAnimationgTile) {
        // console.log("再次进来了吗");
        this.isAnimationgTile = true;
        this.initValue = "visibility: visible;";
      }
      if (glap1 <= 0 && !this.isAnimate1) {
        this.isAnimate1 = true;
        this.initValue1 = "visibility: visible;";
      }
      if (glap2 <= 0 && !this.isAnimate2) {
        this.isAnimate2 = true;
        this.initValue2 = "visibility: visible;";
      }
      if (glap3 <= 0 && !this.isAnimate3) {
        this.isAnimate3 = true;
        this.initValue3 = "visibility: visible;";
      }
      if (glap4 <= 0 && !this.isAnimate4) {
        this.isAnimate4 = true;
        this.initValue4 = "visibility: visible;";
      }
    },
  },
};
</script>

