<template>
  <div class="zy-news">
    <div class="zy-news-content">
      <div
        class="article-title wow fadeInUp"
        :class="isNewsTitle ? 'animated' : ''"
        ref="newsTitle"
        :style="`${initTitleValue}`"
        @animationend="titleEnd5"
      >
        <h2 class="h2-en" style="text-align: center">Latest News</h2>
        <div
          class="title-line specileTile"
          style="left: 50%; transform: translateX(-50%)"
        >
          苏州冠亚投资管理有限公司
        </div>
        <h1
          class="h1-ch specileCase"
          style="left: 50%; transform: translateX(-50%)"
        >
          最新动态
        </h1>
      </div>
      <div class="zy-news-cards">
        <div
          class="news-wrap wow fadeInUp"
          :class="isNewsContent ? 'animated' : ''"
          ref="newsContent"
          :style="`${initContentValue}`"
          @animationend="ContentEnd5"
        >
          <el-carousel :interval="5000" arrow="always">
            <el-carousel-item v-for="item in 3" :key="item">
              <div class="news-item">
                <article class="news-guide">
                  <div class="news-img">
                    <div class="z-scale-animate">
                      <span class="newsWord">NEWS</span>
                    </div>
                  </div>
                  <div class="guide-text">
                    <p class="time-text">
                      融资丨苏州冠亚领投「汉骅半导体」 ——完成数亿元B轮融资
                    </p>
                    <p>
                      近日，苏州汉骅半导体有限公司完成数亿元B轮融资，由苏州冠亚领投，弘晖资本、望睿投资、高新金控等共同投资。
                      ……
                    </p>

                    <div class="showMessage" @click="pushDetail">
                      <div class="view-all">
                        <div>
                          <span class="arrow-icon"> </span>
                        </div>
                        <span class="view-text">查看详情</span>
                        <div class="line"></div>
                      </div>
                    </div>
                  </div>
                </article>
              </div>
            </el-carousel-item>
          </el-carousel>
        </div>
      </div>

      <!-- 更多新闻 -->
        <div class="view-all wrap">
          <div>
            <span class="arrow-icon"> </span>
          </div>
          <span class="view-text specileWord">更多案例</span>
          <div class="line"></div>
        </div>
      
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isNewsTitle: false,
      initTitleValue: "visibility: hidden; animation-name: none;",
      isNewsContent: false,
      initContentValue: "visibility: hidden; animation-name: none;",
    };
  },
  mounted() {
    this.$bus.$on("scrollTopEvent", this.newsMove);
    this.clientHeight = Object.freeze(document.documentElement.clientHeight);
  },
  destroyed() {
    this.$bus.$off("scrollTopEvent", this.newsMove);
  },
  methods: {
    titleEnd5() {
      this.isNewsTitle = false;
    },
    ContentEnd5() {
      this.isNewsContent = false;
    },
    async newsMove(dom) {
      this.isMove = window.scrollY === 0 ? false : true;
      // 标题
      let domTop = this.$refs.newsTitle.getBoundingClientRect().top;
      let glap = domTop - this.clientHeight;

      let contentTop = this.$refs.newsContent.getBoundingClientRect().top;
      let newGlap = contentTop - this.clientHeight;
      // console.log(glap)

      if (glap <= 0 && !this.isNewsTitle) {
        this.isNewsTitle = true;
        this.initTitleValue = "visibility: visible;";
      }
      if (newGlap <= 0 && !this.isNewsContent) {
        this.isNewsContent = true;
        this.initContentValue = "visibility: visible;";
      }
    },
    pushDetail() {
      this.$router.push({
        path: "/newslist",
      });
    },
  },
};
</script>

