import Vue from 'vue'
let app = new Vue({})


/**
 * 事件名称：scrollTopEvent
 * 事件含义：当主区域(BlogDetail)滚动条发生变化时，TocBlog会改变选中效果
 * 事件参数：主区域的dom元素，当主区域元素没有时，那么就为undefined
 * 
 * 事件名称：resetScrollTop
 * 事件含义：设置主区域的scrollTop值
 * 事件参数：设置top的top值
 * 
 *  */
 Vue.prototype.$bus = app //只需要把这个注册在原型身上就可以
export default app