import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
// import DetailView from ""

// join_us
Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'home',
        component: HomeView
    },
    {
        path: '/business',
        name: 'business',
        component: () =>
            import ( /* webpackChunkName: "about" */ '../views/BusinessView.vue'),
        children: [{
            path: "details",
            name: "computerDetail",
            component: () =>
                import ("../components/ComputedIntro/DetailView.vue")
        }, {
            path: "details/teams",
            name: "investmentTeam",
            component: () =>
                import ("../components/ComputedIntro/TeamView.vue")
        }, {
            path: "details/idea",
            name: "investmentIdea",
            component: () =>
                import ("../components/ComputedIntro/IdeaView.vue")
        }, {
            path: "details/change",
            name: "investmentChange",
            component: () =>
                import ("../components/ComputedIntro/ChangeView.vue")
        }]
    },
    {
        path: '/fund',
        name: 'fund',
        component: () =>
            import ( /* webpackChunkName: "about" */ '../views/FundView.vue')
    },
    {
        path: '/case',
        name: 'case',
        component: () =>
            import ( /* webpackChunkName: "about" */ '../views/CaseView.vue'),

    },
    {
        path: '/case/detail/:detailId',
        name: 'caseDetail',
        component: () =>
            import ( /* webpackChunkName: "about" */ '../views/CaseDetailView.vue'),

    },
    {
        path: '/newslist',
        name: 'newslist',
        component: () =>
            import ( /* webpackChunkName: "about" */ '../views/NewsListView.vue')
    }, {
        path: '/newslist/page/:pageId',
        name: 'newsPaging',
        component: () =>
            import ( /* webpackChunkName: "about" */ '../views/NewsListView.vue')
    }, {
        path: '/newslist/newsDetail/:newId',
        name: 'newsDetail',
        component: () =>
            import ( /* webpackChunkName: "about" */ '../components/NewsDetailView/NewsDetailView.vue')
    }, {
        path: '/contact_us',
        name: 'contactUs',
        component: () =>
            import ( /* webpackChunkName: "about" */ '../views/ContactUs.vue')
    }, {
        path: '/contact_us/send_BD',
        name: 'sendBD',
        component: () =>
            import ( /* webpackChunkName: "about" */ '../views/SendView.vue')
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

router.afterEach((to, from, next) => {
    window.scrollTo(0, 0)
})

export default router